<template>
  <div class="subject">
    <h3>Предмет</h3>
    <Loader v-if="loading" />
    <div v-show="!loading" class="input-field width">
      <input
        type="text"
        autocomplete="off"
        ref="autocomplete"
        id="autocomplete-input"
        v-model.trim="inputSubject"
        :class="{ invalid: !checkSubject && inputSubject.length }"
      />
      <label for="autocomplete-input">Введи предмет</label>
      <span
        v-show="!checkSubject"
        class="helper-text"
        data-error="Такого предмета нет"
      ></span>
    </div>
    <div v-show="!loading && checkSubject" class="row width">
      <div class="input-field col s5">
        <input
          type="text"
          autocomplete="off"
          id="first-datepicker"
          ref="first-datepicker"
        />
        <label for="first-datepicker">Выбери начало</label>
      </div>
      <div class="input-field col push-s2 s5">
        <input
          type="text"
          autocomplete="off"
          id="second-datepicker"
          ref="second-datepicker"
        />
        <label for="second-datepicker">Выбери конец</label>
      </div>
    </div>
    <button
      @click="loadSubject"
      v-show="!loading && checkSubject"
      class="btn light-blue darken-1 waves-effect waves-light"
    >
      Загрузить
    </button>
    <div v-show="!loading" id="report" v-html="subject"></div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import Loader from "@/components/Loader.vue";

export default {
  data: () => ({
    subject: "",
    loading: !0,
    subjects: [],
    instances: [],
    inputSubject: ""
  }),
  methods: {
    ...mapActions(["GetRange", "GetSubjectsID", "GetSubject"]),
    init(data) {
      this.subjects = data.subjects;
      const datepickerOptions = {
        format: "dd.mm.yy",
        firstDay: 1,
        minDate: new Date(data.range.start),
        maxDate: new Date(data.range.end),
        i18n: {
          cancel: "Отмена",
          done: "Готово",
          months: [
            "Январь",
            "Февраль",
            "Март",
            "Апрель",
            "Май",
            "Июнь",
            "Июль",
            "Август",
            "Сентябрь",
            "Октябрь",
            "Ноябрь",
            "Декабрь"
          ],
          monthsShort: [
            "Янв",
            "Фев",
            "Мар",
            "Апр",
            "Май",
            "Июн",
            "Июл",
            "Авг",
            "Сен",
            "Окт",
            "Ноя",
            "Дек"
          ],
          weekdays: [
            "Воскресенье",
            "Понедельник",
            "Вторник",
            "Среда",
            "Четверг",
            "Пятница",
            "Суббота"
          ],
          weekdaysShort: ["Вск", "Пнд", "Втр", "Срд", "Чтв", "Птн", "Сбт"],
          weekdaysAbbrev: ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"]
        }
      };
      const autocompleteData = {};
      for (let subject of this.subjects) autocompleteData[subject.title] = null;

      setTimeout(() => {
        this.destroy();
        this.instances.push(
          // eslint-disable-next-line no-undef
          M.Autocomplete.init(this.$refs["autocomplete"], {
            data: autocompleteData,
            limit: 6,
            minLength: 0,
            onAutocomplete: s => (this.inputSubject = s),
            sortFunction(...args) {
              let result = [];
              let str = args.pop().toLowerCase();
              let words = [...args];

              for (let word of words) {
                word = word.toLowerCase();
                let last_index = -1;
                let match_char = 0;
                for (let char of str) {
                  let index = word.indexOf(char);
                  match_char += index < 0 || index < last_index ? -1 : 1;
                  last_index = index;
                }
                result.push(match_char);
              }
              return result.reduce((a, v) => (a = v - a));
            }
          }),
          // eslint-disable-next-line no-undef
          M.Datepicker.init(this.$refs["first-datepicker"], datepickerOptions),
          // eslint-disable-next-line no-undef
          M.Datepicker.init(this.$refs["second-datepicker"], datepickerOptions)
        );
      }, 0);
    },
    destroy() {
      for (let instance of this.instances) {
        if (instance && instance.destroy) {
          instance.destroy();
        }
      }
      this.instances = [];
    },
    setRange(ins, date) {
      ins?.setDate?.(date);
      ins?.setInputValue?.();
    },
    saveRange(id, range) {
      let rangers = {};
      try {
        rangers = JSON.parse(localStorage["vi-sgo-subjects"]);
      } catch (e) {
        localStorage["vi-sgo-subjects"] = {};
      }
      rangers[id] = range;
      localStorage["vi-sgo-subjects"] = JSON.stringify(rangers);
      return range;
    },
    loadRange(id) {
      let rangers = {};
      try {
        rangers = JSON.parse(localStorage["vi-sgo-subjects"]);
      } catch (e) {
        localStorage["vi-sgo-subjects"] = {};
      }
      return {
        start: rangers[id]?.start || null,
        end: rangers[id]?.end || null
      };
    },
    loadSubject() {
      this.loading = true;
      const [, start, end] = this.instances;
      const range = this.saveRange(this.IDSubject, {
        start: start.toString("yyyy-mm-dd"),
        end: end.toString("yyyy-mm-dd")
      });
      this.GetSubject({
        id: this.IDSubject,
        ...range
      })
        .then(html => ((this.loading = false), (this.subject = html)))
        .catch(err => {
          this.loading = false;
          if (err.code == 4008) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Сайт лег, попробуй позже" });
          } else if (err.code == 4006) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Ошибка парсера, попробуй ещё раз" });
          } else {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Неизвестная ошибка, попробуй ещё раз" });
          }
          console.error(err);
        });
    }
  },
  watch: {
    checkSubject(val) {
      if (!val) return;
      const range = this.loadRange(this.IDSubject);
      const [, start, end] = this.instances;
      this.setRange(start, range.start);
      this.setRange(end, range.end);
    }
  },
  computed: {
    IDSubject() {
      return this.subjects.find(
        s => s.title.toLowerCase() == this.inputSubject.toLowerCase()
      )?.value;
    },
    checkSubject() {
      return this.subjects.find(
        s => s.title.toLowerCase() == this.inputSubject.toLowerCase()
      );
    }
  },
  created() {
    const data = {};
    this.loading = true;
    this.GetSubjectsID()
      .then(subjects => ((data.subjects = subjects), this.GetRange()))
      .then(range => ((data.range = range), this.init(data)))
      .then(() => (this.loading = false))
      .catch(err => {
        this.loading = false;
        if (err.code == 4008) {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Сайт лег, попробуй позже" });
        } else if (err.code == 4006) {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Ошибка парсера, попробуй ещё раз" });
        } else {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Неизвестная ошибка, попробуй ещё раз" });
        }
        console.error(err);
      });
  },
  destroyed() {
    this.destroy();
  },
  components: {
    Loader
  }
};
</script>
